import { TransformationTags } from "../../calculation/TransformationTags";
import { PlayerContextState } from "../../PlayerContext";
import { getResourceAmount, grantResource, Resource } from "../../Resources";
import { getTotalElementAndSchoolExp } from "../../spells/ElementsAndSchools";
import { getSecondsPlayed } from "../../timetick/TotalTimePlayed";
import {
  ActionBase,
  ActionEffect,
  ActionSubcategory,
  DoActionArgs,
} from "../Action";

class UseTimePiecesEvent extends ActionBase {
  constructor() {
    super();
  }

  isVisible(state: PlayerContextState): boolean {
    return (
      getResourceAmount(state, Resource.TimePieces) > 0 &&
      getTotalElementAndSchoolExp(state) > 10000
    );
  }

  getName(): string {
    return "Use Time Pieces (Event)";
  }

  getArea(): string {
    return "Campus";
  }

  getSubcategory(): ActionSubcategory | undefined {
    return ActionSubcategory["Use Time Pieces"];
  }

  getDisplayDescription(): string {
    return "Use your time pieces to trigger a random event.";
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      timePiecesPerEvent: {
        value: 3000,
        tags: [TransformationTags.TimePiecePerEvent],
      },
    };
  }

  getDisplayEffect(state: PlayerContextState): string {
    return `Immediately trigger the next random event`;
  }

  canAfford(state: PlayerContextState): boolean {
    return (
      super.canAfford(state) &&
      getResourceAmount(state, Resource.TimePieces) >= 3000
    );
  }

  getCost(state: PlayerContextState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        "Time Pieces": 3000,
      },
      items: {},
    };
  }

  doAction(args: DoActionArgs, state: PlayerContextState): PlayerContextState {
    state = grantResource(
      Resource.TimePieces,
      -(this.getCost(state).resources["Time Pieces"] ?? 0),
    )(state);
    const delta = state.nextRandomEvent - getSecondsPlayed(state);
    state.nextRandomEvent = getSecondsPlayed(state);
    state.pityEnabledAtSeconds = (state.pityEnabledAtSeconds ?? 0) - delta;
    return state;
  }
}

export default new UseTimePiecesEvent();
