import { color } from "@rneui/base";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import {
  Platform,
  StyleProp,
  StyleSheet,
  TextStyle,
  TouchableNativeFeedback,
} from "react-native";
import {
  Button as RNEButton,
  ButtonProps,
  useTheme,
} from "react-native-elements";
import { disabledColorByTheme } from "./Miscellaneous";

export function Button(
  props: Omit<ButtonProps, "onPress"> & {
    onPress: () => void;
    autoRepressConfig?: {
      minTimeMs?: number;
      startingThreshold?: number;
      startingRepressTime?: number;
      dropoffMultiplier?: number;
    };
  },
) {
  const theme = useTheme();
  const { disabledTitleStyle, ...rest } = props;
  const disabledTitleStyleComputed = useMemo(
    () => ({
      color: disabledColorByTheme(theme.theme),
    }),
    [theme],
  );
  const pressed = useRef(false);
  const disabled = useRef(props.disabled);
  disabled.current = props.disabled;
  const currentRepressTimeout = useRef(300);
  const currentRepressHandler = useRef<NodeJS.Timeout | null>(null);
  const [flash, setFlash] = useState(false);
  useEffect(() => {
    if (flash) {
      setFlash(false);
    }
  }, [flash]);
  const rePress = useCallback(() => {
    if (pressed.current && !disabled.current) {
      props.onPress && props.onPress();
      setFlash(true);
      currentRepressTimeout.current = Math.max(
        props?.autoRepressConfig?.minTimeMs ?? 100,
        currentRepressTimeout.current *
          (props?.autoRepressConfig?.dropoffMultiplier ?? 0.94),
      );
      currentRepressHandler.current = setTimeout(
        rePress,
        currentRepressTimeout.current,
      );
    }
  }, [props.onPress]);
  const onPressInCallback = useCallback(() => {
    pressed.current = true;
    currentRepressHandler.current = setTimeout(() => {
      if (pressed.current && !disabled.current) {
        currentRepressTimeout.current =
          props?.autoRepressConfig?.startingRepressTime ?? 300;
        rePress();
      }
    }, props?.autoRepressConfig?.startingRepressTime ?? 600);
  }, []);
  const onPressOutCallback = useCallback(() => {
    pressed.current = false;
    if (currentRepressHandler.current != null) {
      clearTimeout(currentRepressHandler.current);
      currentRepressHandler.current = null;
    }
  }, []);
  const titleStyle: StyleProp<TextStyle> = StyleSheet.flatten([
    {
      color: "white",
    },
    props.titleStyle,
  ]);
  const background =
    Platform.OS === "android" && Platform.Version >= 21 && !flash
      ? TouchableNativeFeedback.Ripple(
          color(titleStyle?.color?.toString()).alpha(0.32).rgb().string(),
          true,
        )
      : undefined;
  return (
    <RNEButton
      disabledTitleStyle={[disabledTitleStyleComputed, disabledTitleStyle]}
      onPressIn={onPressInCallback}
      onPressOut={onPressOutCallback}
      {...rest}
      background={background}
      containerStyle={[
        props.containerStyle,
        flash && { backgroundColor: theme.theme.colors.primary },
      ]}
    />
  );
}
