import { Picker as RNPicker, PickerProps } from "@react-native-picker/picker";
import { useTheme } from "react-native-elements";

export function Picker<T>(
  props: Omit<Omit<PickerProps, "selectedValue">, "onValueChange"> & {
    selectedValue: T;
    onValueChange: (itemValue: T, itemIndex: number) => void;
  },
) {
  const { children, style, ...rest } = props;
  const theme = useTheme();
  return (
    <RNPicker
      {...rest}
      style={[
        {
          color: theme.theme.colors.black,
          backgroundColor: theme.theme.colors.grey5,
        },
        style,
      ]}
      dropdownIconColor={theme.theme.colors.black}
      dropdownIconRippleColor={theme.theme.colors.black}
    >
      {children}
    </RNPicker>
  );
}
