const dividerColorDark =
  StyleSheet.hairlineWidth < 1 ? "#666" : "rgba(255, 255, 255, 0.12)";

const lightCommonColors = {
  hpBarForeground: "#f00",
  hpBarBackground: "#800",
  progressBar: "#07f",
  disabled: "#eee",
  grey0: "#333",
  grey1: "#444",
  grey2: "#666",
  grey3: "#888",
  grey4: "#ccc",
  grey5: "#eee",
  cappedResource: "#00c",
};

const darkCommonColors = {
  divider: dividerColorDark,
  disabled: "#363636",
  grey5: "#303030",
  grey4: "#444",
  grey3: "#666",
  grey2: "#888",
  grey1: "#ccc",
  grey0: "#eee",
  background: "#222",
  hpBarForeground: "#a00",
  hpBarBackground: "#500",
  progressBar: "#06c",
  cappedResource: "#bbf",
  error: "#f00",
};

const primaryColorsByElement: Record<string, any> = {
  Default: {
    lightColors: { ...lightCommonColors },
    darkColors: { primary: "#247", ...darkCommonColors },
  },
  Fire: {
    lightColors: { primary: "#c30", ...lightCommonColors },
    darkColors: {
      primary: "#820",
      ...darkCommonColors,
    },
  },
  Earth: {
    lightColors: { primary: "#740", ...lightCommonColors },
    darkColors: {
      primary: "#630",
      ...darkCommonColors,
    },
  },
  Water: {
    lightColors: { primary: "#08f", ...lightCommonColors },
    darkColors: {
      primary: "#048",
      ...darkCommonColors,
    },
  },
  Air: {
    lightColors: { primary: "#0b4", ...lightCommonColors },
    darkColors: {
      primary: "#063",
      ...darkCommonColors,
    },
  },
  Nature: {
    lightColors: { primary: "#5b0", ...lightCommonColors },
    darkColors: {
      primary: "#360",
      ...darkCommonColors,
    },
  },
  Lightning: {
    lightColors: { primary: "#80f", ...lightCommonColors },
    darkColors: {
      primary: "#408",
      ...darkCommonColors,
    },
  },
  Darkness: {
    lightColors: { primary: "#444", ...lightCommonColors },
    darkColors: {
      primary: "#444",
      ...darkCommonColors,
    },
  },
};

function overrideShouldComponentUpdate(
  this: any,
  nextProps: any,
  nextState: any,
) {
  return (
    this.props.theme != nextProps.theme ||
    JSON.stringify(this.state) != JSON.stringify(nextState)
  );
}

const memoizedThemes: Record<string, any> = {};
const memoizedTheme = (key: any, theme: any) => {
  const stringified = JSON.stringify(key);
  if (!memoizedThemes[stringified]) {
    memoizedThemes[stringified] = theme;
  }
  return memoizedThemes[stringified];
};

function overrideRender(this: any) {
  const value = memoizedTheme(this.state.theme, {
    theme: this.state.theme,
    updateTheme: this.updateTheme,
    replaceTheme: this.replaceTheme,
  });
  return (
    <ThemeContext.Provider value={value}>
      {this.props.children}
    </ThemeContext.Provider>
  );
}
ThemeProvider.prototype.shouldComponentUpdate = overrideShouldComponentUpdate;
ThemeProvider.prototype.render = overrideRender;

function ThemeUpdater() {
  const theme = useTheme();
  const playerContext = useContext(PlayerContext);

  const fullTheme = useMemo((): CreateThemeOptions => {
    return {
      lightColors:
        primaryColorsByElement?.[playerContext.primaryElement || "Default"]
          ?.lightColors,
      darkColors:
        primaryColorsByElement?.[playerContext.primaryElement || "Default"]
          ?.darkColors,
      mode: playerContext.global.themeMode,
    };
  }, [
    primaryColorsByElement,
    playerContext.primaryElement,
    playerContext.global.themeMode,
  ]);

  useEffect(() => {
    theme.replaceTheme(fullTheme);
  }, [fullTheme]);

  return null;
}

function IntroContents() {
  const theme = useTheme();
  return (
    <View style={styles.maxContainer}>
      <Header
        containerStyle={[
          styles.headerContainer,
          { borderBottomColor: theme.theme.colors.background },
        ]}
        statusBarProps={{
          translucent: true,
          backgroundColor: "transparent",
          barStyle: "light-content",
        }}
        centerComponent={{
          text: "Welcome to Magic Research",
          style: { color: "#fff" },
        }}
      />
      <OrientationLockManager />
      <SafeAreaView style={styles.safeAreaContainer}>
        <View
          style={[
            styles.biggerContainer,
            { backgroundColor: theme.theme.colors.background },
          ]}
        >
          <View style={styles.introContainer}>
            <ScrollView contentContainerStyle={styles.container}>
              <Intro />
            </ScrollView>
          </View>
        </View>
      </SafeAreaView>
    </View>
  );
}

function MainAppContents(props: { setPlayerState: any }) {
  const { height, width } = useWindowDimensions();
  const playerContext = useContext(PlayerContext);

  const [menuShown, setMenuShown] = useState(false);
  const [rightMenuShown, setRightMenuShown] = useState("none");

  const [section, setSection] = useState("Campus");
  const theme = useTheme();

  const processUICommand = useCallback((uiCommand: string) => {
    if (uiCommand == "goToExploration") {
      setSection("Exploration");
    } else if (uiCommand == "goToEnding") {
      setSection("Ending");
    } else if (uiCommand == "closeAllMenus") {
      setMenuShown(false);
      setRightMenuShown("none");
    }
  }, []);

  const processUICommands = useCallback((state: PlayerContextState) => {
    const uiCommands = getUICommands(state);
    uiCommands.forEach(processUICommand);
    state.uiCommands = uiCommands.filter(
      (uiCommand) =>
        uiCommand != "goToExploration" &&
        uiCommand != "goToEnding" &&
        uiCommand != "closeAllMenus",
    );
    return state;
  }, []);

  useEffect(() => {
    addContextListener("appUiCommand", (oldState, newState) => {
      const uiCommands = getUICommands(newState);
      if (uiCommands.length > 0) {
        return (state) => processUICommands(state);
      }
    });
  }, []);

  const onMenuPress = useCallback(() => {
    setMenuShown(!menuShown);
    setRightMenuShown("none");
  }, [menuShown]);
  const onEventMenuPress = useCallback(() => {
    if (rightMenuShown !== "event") {
      setRightMenuShown("event");
    } else {
      setRightMenuShown("none");
    }
    setMenuShown(false);
  }, [rightMenuShown]);
  const onSpellMenuPress = useCallback(() => {
    if (rightMenuShown !== "spell") {
      setRightMenuShown("spell");
    } else {
      setRightMenuShown("none");
    }
    setMenuShown(false);
  }, [rightMenuShown]);
  const onNavigationPress = useCallback((section: string) => {
    setSection(section);
    setMenuShown(false);
  }, []);

  const ongoingEventCount = Object.values(
    playerContext.eventOccurrences,
  ).filter((event) => {
    return (
      event.currentMessageId &&
      !getEventMessageById(event.currentMessageId).isTerminal() &&
      getEventMessageById(event.currentMessageId).isActionable(
        playerContext,
        event.params,
      )
    );
  }).length;

  const hasFourColumns = width >= 1250;
  const hasTwoColumnsOrMore = width >= 700;

  const showBottomResourceBar =
    Platform.OS !== "web" &&
    !hasTwoColumnsOrMore &&
    ![
      "Exploration",
      "Inventory",
      "Retirement",
      "Stats",
      "Help",
      "Options",
      "About",
      "Debug",
    ].includes(section);

  const centerComponent = useMemo(
    () => (
      <View style={styles.centerText}>
        <Text style={{ color: "#fff" }}>{section}</Text>
      </View>
    ),
    [section, theme],
  );

  const bottomOpacityAnim = useRef(
    new Animated.Value(showBottomResourceBar ? 0.5 : 0),
  ).current;

  useEffect(() => {
    if (showBottomResourceBar) {
      Animated.timing(bottomOpacityAnim, {
        toValue: 1.0,
        duration: 150,
        useNativeDriver: true,
      }).start();
    } else {
      Animated.timing(bottomOpacityAnim, {
        toValue: 0,
        duration: 150,
        useNativeDriver: true,
      }).start();
    }
  }, [showBottomResourceBar]);

  const mainSection = useMemo(
    () =>
      section === "Exploration" ? (
        <View style={styles.mainContainer}>
          <Exploration />
        </View>
      ) : (
        <ScrollView
          style={styles.mainContainer}
          contentContainerStyle={styles.container}
        >
          {section === "Campus" && <Campus />}
          {section === "Research" && <Research />}
          {section === "Apprentices" && <Autocast />}
          {section === "Inventory" && <Inventory />}
          {section === "Upgrades" && <Upgrades />}
          {section === "Stats" && <Stats />}
          {section === "Creatures" && <Creatures />}
          {section === "Strengthening" && <Strengthening />}
          {section === "Retirement" && <Retirement />}
          {section === "Ending" && <Ending />}
          {section === "Options" && <Options />}
          {section === "About" && <About />}
          {section === "Help" && <Help />}
          {section === "Debug" && <Debug />}
        </ScrollView>
      ),
    [section],
  );

  const swipeLeftHandler = useCallback(
    (state: PanResponderGestureState) => {
      if (Math.abs(state.dx) > Math.abs(state.dy)) {
        if (menuShown) {
          setMenuShown(false);
        } else if (rightMenuShown === "none") {
          setRightMenuShown("spell");
        }
      }
    },
    [menuShown, setMenuShown, rightMenuShown, setRightMenuShown],
  );
  const swipeRightHandler = useCallback(
    (state: PanResponderGestureState) => {
      if (Math.abs(state.dx) > Math.abs(state.dy)) {
        if (rightMenuShown != "none") {
          setRightMenuShown("none");
        } else if (!menuShown && !hasTwoColumnsOrMore) {
          setMenuShown(true);
        }
      }
    },
    [menuShown, setMenuShown, rightMenuShown, setRightMenuShown],
  );

  return useMemo(() => {
    if (hasTwoColumnsOrMore) {
      return (
        <View
          style={[
            styles.maxContainer,
            { backgroundColor: theme.theme.colors.background },
          ]}
        >
          <GameTickProcessor setPlayerState={props.setPlayerState} />
          <ThemeUpdater />
          <Header
            containerStyle={[
              styles.headerContainer,
              { borderBottomColor: theme.theme.colors.background },
            ]}
            statusBarProps={{
              translucent: true,
              backgroundColor: "transparent",
              barStyle: "light-content",
            }}
            leftComponent={undefined}
            centerComponent={centerComponent}
            rightComponent={
              !hasFourColumns ? (
                <View style={styles.rightHeaderContainer}>
                  <TouchableOpacity
                    style={styles.rightHeaderIcon}
                    onPressIn={onSpellMenuPress}
                    hitSlop={{ top: 20, left: 20, bottom: 20, right: 20 }}
                  >
                    <Icon
                      name="magic-wand"
                      type="simple-line-icon"
                      size={24}
                      color="#fff"
                    />
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={styles.rightHeaderIcon}
                    onPressIn={onEventMenuPress}
                    hitSlop={{ top: 20, left: 10, bottom: 20, right: 20 }}
                  >
                    <Icon name="notifications" color="#fff" />
                    {ongoingEventCount > 0 && (
                      <Badge
                        value={ongoingEventCount}
                        status="error"
                        containerStyle={styles.notificationsBadge}
                      />
                    )}
                  </TouchableOpacity>
                </View>
              ) : undefined
            }
          />
          <SafeAreaView style={styles.safeAreaContainer}>
            <EventOverlay />
            <GestureRecognizer
              style={styles.gestureRecognizer}
              onSwipeLeft={swipeLeftHandler}
              onSwipeRight={swipeRightHandler}
              config={{ directionalOffsetThreshold: 200 }}
            >
              <View
                style={[
                  styles.biggerContainer,
                  { backgroundColor: theme.theme.colors.background },
                ]}
              >
                <View
                  style={[
                    styles.horizontalContainer,
                    Platform.OS === "web" && hasFourColumns
                      ? { height: height - 66 }
                      : Platform.OS === "web"
                      ? { height: height - 76 }
                      : { flex: 1 },
                  ]}
                >
                  <View style={styles.webMenuContainer}>
                    <MenuContents
                      visible={true}
                      onNavigationPress={onNavigationPress}
                    />
                  </View>
                  {mainSection}
                  {hasFourColumns ? (
                    <View style={styles.webRightMenuContainer}>
                      <SpellMenuContents visible={true} />
                    </View>
                  ) : (
                    <SpellMenu
                      visible={rightMenuShown === "spell"}
                      onBackgroundPress={onSpellMenuPress}
                    />
                  )}
                  {hasFourColumns ? (
                    <View style={styles.webRightMenuContainer}>
                      <ScrollView>
                        <EventList />
                      </ScrollView>
                    </View>
                  ) : (
                    <EventMenu
                      visible={rightMenuShown === "event"}
                      onBackgroundPress={onEventMenuPress}
                    />
                  )}
                </View>

                <ManaBar />
              </View>
            </GestureRecognizer>
          </SafeAreaView>
        </View>
      );
    }
    return (
      <View
        style={[
          styles.maxContainer,
          { backgroundColor: theme.theme.colors.background },
        ]}
      >
        <GameTickProcessor setPlayerState={props.setPlayerState} />
        <ThemeUpdater />
        <Header
          containerStyle={[
            styles.headerContainer,
            { borderBottomColor: theme.theme.colors.background },
          ]}
          statusBarProps={{
            translucent: true,
            backgroundColor: "transparent",
            barStyle: "light-content",
          }}
          leftComponent={
            <TouchableOpacity
              onPressIn={onMenuPress}
              hitSlop={{ top: 20, left: 20, bottom: 20, right: 20 }}
            >
              <Icon name="menu" color="#fff" />
            </TouchableOpacity>
          }
          centerComponent={centerComponent}
          rightComponent={
            <View style={styles.rightHeaderContainer}>
              <TouchableOpacity
                style={styles.rightHeaderIcon}
                onPressIn={onSpellMenuPress}
                hitSlop={{ top: 20, left: 20, bottom: 20, right: 20 }}
              >
                <Icon
                  name="magic-wand"
                  type="simple-line-icon"
                  size={24}
                  color="#fff"
                />
              </TouchableOpacity>
              <TouchableOpacity
                style={styles.rightHeaderIcon}
                onPressIn={onEventMenuPress}
                hitSlop={{ top: 20, left: 10, bottom: 20, right: 20 }}
              >
                <Icon name="notifications" color="#fff" />
                {ongoingEventCount > 0 && (
                  <Badge
                    value={ongoingEventCount}
                    status="error"
                    containerStyle={styles.notificationsBadge}
                  />
                )}
              </TouchableOpacity>
            </View>
          }
        />
        <SafeAreaView style={styles.safeAreaContainer}>
          <EventOverlay />
          <GestureRecognizer
            style={styles.gestureRecognizer}
            onSwipeLeft={swipeLeftHandler}
            onSwipeRight={swipeRightHandler}
            config={{ directionalOffsetThreshold: 200 }}
          >
            <View
              style={[
                styles.biggerContainer,
                { backgroundColor: theme.theme.colors.background },
              ]}
            >
              <View
                style={[
                  Platform.OS === "web" ? { height: height - 76 } : { flex: 1 },
                  styles.mainContainerPhone,
                ]}
              >
                <View style={{ flex: 1 }}>
                  {mainSection}
                  <SpellMenu
                    visible={rightMenuShown === "spell"}
                    onBackgroundPress={onSpellMenuPress}
                  />
                  <EventMenu
                    visible={rightMenuShown === "event"}
                    onBackgroundPress={onEventMenuPress}
                  />
                </View>
                {showBottomResourceBar && (
                  <Animated.View
                    style={[
                      {
                        opacity: bottomOpacityAnim,
                        borderTopWidth: 1.0,
                        borderTopColor: theme.theme.colors.divider,
                      },
                    ]}
                  >
                    <BottomResourceViewer />
                  </Animated.View>
                )}
                <Menu
                  visible={menuShown}
                  onBackgroundPress={onMenuPress}
                  onNavigationPress={onNavigationPress}
                />
              </View>

              <ManaBar />
            </View>
          </GestureRecognizer>
        </SafeAreaView>
      </View>
    );
  }, [
    props.setPlayerState,
    onMenuPress,
    onSpellMenuPress,
    onEventMenuPress,
    section,
    mainSection,
    ongoingEventCount,
    menuShown,
    rightMenuShown,
    onNavigationPress,
    hasTwoColumnsOrMore,
    hasFourColumns,
    height,
    bottomOpacityAnim,
    theme,
  ]);
}

export default function App() {
  const [playerState, setPlayerState] = useState(PLAYER_INITIAL_STATE);
  const [localUiState, setLocalUiState] = useState<any>({});

  const uiContextValue = useMemo(() => {
    return {
      getUIState: (id: string) => {
        return localUiState?.[id];
      },
      setUIState: (id: string, value: any) => {
        localUiState[id] = value;
        setLocalUiState(localUiState);
      },
    };
  }, [localUiState, setLocalUiState]);

  const [loading, setLoading] = useState(true);

  const playerStateRef = useRef(playerState);
  playerStateRef.current = playerState;

  const apply = useCallback(
    (transform: PlayerContextTransform) => {
      const newState = applyAllTransforms(playerStateRef.current, transform);
      newState.global.appVersionTimestamp = getCurrentVersionTimestamp() || 0;
      newState.global.isFullVersionSave = !Demo.isDemo();

      setPlayerState(newState);
    },
    [playerStateRef, setPlayerState],
  );

  const overwrite = useCallback(
    (transform: PlayerContextTransform) => {
      setPlayerState(transform(playerStateRef.current));
    },
    [playerStateRef, setPlayerState],
  );

  const playerContextValue = useMemo(() => {
    return {
      ...playerState,
      apply: apply,
      overwrite: overwrite,
    };
  }, [playerState, apply, overwrite]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const loadedState = await loadData();
        setPlayerState(loadedState);
        setLoading(false);
      } catch (error) {
        console.error("Error loading data");
      }
    };

    fetchData();
  }, []);

  useEffect(() => {
    const storeData = async () => {
      try {
        await saveData(playerState);
      } catch (error) {
        console.error("Error saving data", error);
      }
    };
    storeData();
  }, [Math.floor(getSecondsPlayed(playerState) / 5.0)]);

  const theme = useMemo(() => {
    return createTheme({
      lightColors:
        primaryColorsByElement?.[playerContextValue.primaryElement || "Default"]
          ?.colors,
      darkColors:
        primaryColorsByElement?.[playerContextValue.primaryElement || "Default"]
          ?.colors,

      mode: playerContextValue.global.themeMode,
    });
  }, [
    primaryColorsByElement,
    playerContextValue.primaryElement,
    playerContextValue.global.themeMode,
  ]);

  if (loading) {
    return (
      <SafeAreaProvider>
        <OrientationLockManager />
        <SafeAreaView style={[styles.safeAreaContainer]}>
          <ActivityIndicator size="large" />
        </SafeAreaView>
      </SafeAreaProvider>
    );
  }

  if (
    playerContextValue.primaryElement == undefined ||
    playerContextValue.primarySchool == undefined
  ) {
    // Send to intro - do not have the GameTickProcessor or any menu
    return (
      <SafeAreaProvider>
        <PlayerContext.Provider value={playerContextValue}>
          <UIContext.Provider value={uiContextValue}>
            <ThemeProvider theme={theme}>
              <ThemeUpdater />
              <IntroContents />
            </ThemeProvider>
          </UIContext.Provider>
        </PlayerContext.Provider>
      </SafeAreaProvider>
    );
  }

  return (
    <SafeAreaProvider>
      <OrientationLockManager />
      <PlayerContext.Provider value={playerContextValue}>
        <UIContext.Provider value={uiContextValue}>
          <ThemeProvider theme={theme}>
            <MainAppContents setPlayerState={setPlayerState} />
          </ThemeProvider>
        </UIContext.Provider>
      </PlayerContext.Provider>
    </SafeAreaProvider>
  );
}

