const headerPadding =
  Platform.OS === "android" ? 16 : Platform.OS === "web" ? 8 : 0;

const styles = StyleSheet.create({
  headerContainer: {
    paddingTop: headerPadding,
  },
  rightHeaderContainer: {
    flexDirection: "row",
  },
  rightHeaderIcon: {
    marginLeft: 20,
  },
  gestureRecognizer: {
    flex: 1,
  },
  safeAreaContainer: {
    flex: 1,
    justifyContent: "center",
  },
  centerAligned: {
    alignItems: "center",
  },
  maxContainer: {
    flex: 1,
  },
  biggerContainer: {
    flex: 1,
    backgroundColor: "#fff",
  },
  mainContainerPhone: {
    overflow: "hidden",
  },
  introContainer: {
    alignItems: "center",
  },
  horizontalContainer: {
    flexDirection: "row",
    alignItems: "stretch",
    overflow: "hidden",
  },
  mainContainer: {
    flex: 1,
  },
  webMenuContainer: {
    width: 284,
    paddingRight: 8,
  },
  webRightMenuContainer: {
    width: 300,
    paddingLeft: 8,
  },
  notificationsBadge: {
    position: "absolute",
    top: -8,
    right: -8,
  },
  menuContainer: {
    position: "absolute",
  },
  container: {
    padding: 8,
  },
  centerText: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
});
